import React, { useEffect, useState } from "react";
import "./Message.css"; 

function Message() {
    const [messages, setMessages] = useState([]);

    useEffect(() => {
        fetch("http://YuliaGracheva.github.io/suzdal_gymnazia/api/message")
            .then((res) => res.json())
            .then((data) => {
                console.log(" :", data);
                setMessages(data);
            })
            .catch((err) => console.error("  :", err));
    }, []);

    return (
        <div className="message-container">
            <p> : <a href="/"></a> / <a href="/main-info"> </a> / </p>
            <h1 className="message-title"></h1>
            <div className="purple-line"></div>
            {messages.map((msg) => (
                <div key={msg.MessageID} className="message-card">
                    <div className="message-theme">{msg.MessageTheme}</div>
                    <div className="message-description" dangerouslySetInnerHTML={{ __html: msg.MessageDescription }} />
                    <div className="message-author">: {msg.Username}</div>
                </div>
            ))}
        </div>
    );
}

export default Message;
