import React, { useState, useContext, useEffect } from "react";
import { Button, Container, Form, FormControl, Nav, Navbar, NavDropdown } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import { SearchContext } from "./SearchContext.js";
import './Header.css';
import defaultLogo from '../img/image.png';

export default function Header() {
    const [dropdowns, setDropdowns] = useState({
        info: false,
        education: false,
        about: false,
        reception: false,
        resurs: false
    });

    const { setQuery } = useContext(SearchContext);
    const [input, setInput] = useState("");
    const [customLogo, setCustomLogo] = useState(null);
    const navigate = useNavigate();

    useEffect(() => {
        fetch("http://YuliaGracheva.github.io/suzdal_gymnaziaa/api/settings")
            .then((res) => res.json())
            .then((data) => {
                if (data.logo) {
                    setCustomLogo(data.logo);
                }
            })
            .catch(err => {
                console.error("  :", err);
            });
    }, []);    

    const handleSearch = (e) => {
        e.preventDefault();
        setQuery(input);
        navigate("/search");
    };

    const handleToggle = (menu) => {
        setDropdowns(prev => ({
            ...prev,
            [menu]: !prev[menu]
        }));
    };

    const handleClose = (menu) => {
        setDropdowns(prev => ({
            ...prev,
            [menu]: false
        }));
    };

    return (
        <>
            <Navbar collapseOnSelect expand="md" className="header-main">
                <Container>
                    <Navbar.Brand as={Link} to="/">
                        <img
                            src={customLogo || defaultLogo}
                            height={30}
                            alt="Logo"
                            style={{ objectFit: 'contain', maxWidth: "100%" }}
                        />
                    </Navbar.Brand>
                    <p>  </p>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="mr-auto">
                            <Nav.Link as={Link} to="/"></Nav.Link>

                            <NavDropdown title=" " show={dropdowns.info}
                                onMouseEnter={() => handleToggle('info')}
                                onMouseLeave={() => handleClose('info')}>

                                <NavDropdown.Item as={Link} to="/main-info/managment-bodies">   </NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/main-info/documents"></NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/main-info/accessible-environment"> </NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/main-info/paid-educational-services">  </NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/main-info/financial-economic-activity">- </NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/main-info/vacant-place"> </NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/main-info/scholarships"></NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/main-info/international-coop"> </NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/main-info/organisation-eat"> </NavDropdown.Item>

                                <NavDropdown title="" drop="end" show={dropdowns.education}
                                    onMouseEnter={() => handleToggle('education')}
                                    onMouseLeave={() => handleClose('education')}>

                                    <NavDropdown.Item as={Link} to="/main-info/education/gia"></NavDropdown.Item>
                                    <NavDropdown.Item as={Link} to="/main-info/education/education-process"> </NavDropdown.Item>
                                </NavDropdown>
                            </NavDropdown>

                            <NavDropdown title=" " show={dropdowns.about}
                                onMouseEnter={() => handleToggle('about')}
                                onMouseLeave={() => handleClose('about')}>
                                <NavDropdown.Item as={Link} to="/about/leadership"></NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/about/contact"></NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/about/olympiads"></NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/about/message"></NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/about/employee"> </NavDropdown.Item>
                            </NavDropdown>

                            <Nav.Link as={Link} to="/news"></Nav.Link>

                            <NavDropdown.Item as={Link} to="/process-reception"> </NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/fuctional-gramm"> </NavDropdown.Item>
                            
                        </Nav>
                        <div className="header-search-wrapper">
                            <Form className="d-flex" onSubmit={handleSearch}>
                                <FormControl
                                    type="text"
                                    placeholder=""
                                    className="mr-sm-2"
                                    value={input}
                                    onChange={(e) => setInput(e.target.value)}
                                />
                                <Button type="submit" variant="outline-info"></Button>
                            </Form>
                        </div>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </>
    );
}
