import React, { Component } from "react";
import './css/contacts.css';

class Contact extends Component {
    constructor(props) {
            super(props);
            this.state = {
                contactInfo: {
                    address: '',
                    phones: '',
                    email: ''
                },
                recaptchaToken: null,
                useRecaptcha: false
            };
        }

        componentDidMount() {
            fetch("http://YuliaGracheva.github.io/suzdal_gymnazia/api/settings")
                .then((res) => res.json())
                .then((data) => {
                    if (!data) return;
        
                    const contacts = JSON.parse(data.contacts || "{}");
                    
                    this.setState({
                        contactInfo: contacts,
                    });
                })
                .catch((err) => {
                    console.error("   :", err);
                });
        }    

    render() {
        return (
            <div className="contact-main-info">
                <p> : <a href="/"></a> / <a href="/about"> </a> / </p>

                <h1></h1>
                <div className="purple-line"></div>

                <div className="contact-info">
                    <div className="purple-contact-block">
                        <div className="white-contact-block">
                            <h2>  </h2>
                            <p className="contact-text">
                                {this.state.contactInfo.address && (
                                    <>
                                        {this.state.contactInfo.address}<br />
                                    </>
                                )}
                                {this.state.contactInfo.phones && (
                                    <>
                                        :<br />
                                        {this.state.contactInfo.phones}<br />
                                    </>
                                )}
                                {this.state.contactInfo.email && (
                                    <>
                                        E-mail:<br />
                                        <a href={`mailto:${this.state.contactInfo.email}`}>{this.state.contactInfo.email}</a>
                                    </>
                                )}
                            </p>
                        </div>
                        <div className="white-contact-block">
                            <h2>  </h2>
                            <pre>
                                 3310003536
                                331001001
                                 1023302552657
                                /40703810810000001476
                                  8611
                                  . 
                                / 30101810000000000602
                                041708602
                            </pre>
                        </div>
                    </div>
                    <div className="purple-contact-block">
                        <div className="white-contact-block">
                            <h2>    </h2>
                            <button class="button-donat">     100 .</button>
                            <button class="button-donat">     300 .</button>
                            <button class="button-donat">     500 .</button>
                            <button class="button-donat">     1 000 .</button>
                            <button class="button-donat">     3 000 .</button>
                            <button class="button-donat">     5 000 .</button>
                            <button class="button-donat">     10 000 .</button>
                            <button class="button-donat">     50 000 .</button>

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Contact;