import React, { useEffect, useState } from "react";
import "./Olympiad.css";

const Olympiad = () => {
    const [olympiads, setOlympiads] = useState([]);

    useEffect(() => {
        console.log(" Olympiad ");
        fetch("http://YuliaGracheva.github.io/suzdal_gymnazia/api/olympiads")
            .then(res => res.json())
            .then(data => {
                console.log(" :", data);
                setOlympiads(data);
            })
            .catch(error => console.error("   :", error));
    }, []);
    

    return (
        <div className="olympiad-main-info">
            <p> : <a href="/"></a> / <a href="/main-info"> </a> / </p>
            <h1></h1>
            <div className="table-wrapper">
                <table className="olympiad-table">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {olympiads.map((olymp, index) => (
                            <tr key={index}>
                                <td>{olymp.OlympiadSurname}</td>
                                <td>{olymp.OlympiadName}</td>
                                <td>{olymp.OlympiadClass}</td>
                                <td>{olymp.SubjectName}</td>
                                <td>{olymp.StatusName}</td>
                                <td>{olymp.OlympiadQuanityPoints}</td>
                                <td>{olymp.OlympiadPlace}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
};

export default Olympiad;
