import React, { Component } from "react";
import './ManagmentBodies.css';
class ManagmentBodies extends Component {
    state = {
        bodies: []
    };

    componentDidMount() {
        fetch("http://YuliaGracheva.github.io/suzdal_gymnazia/api/managementbodies")
            .then((res) => res.json())
            .then((data) => {
                console.log("   :", data);
                this.setState({ bodies: data });
            })
            .catch((err) => console.error("  :", err));
    }

    render() {
        const { bodies } = this.state;

        return (
            <div className="managment-bodies-main-info">
                <p> : <a href="/"></a> / <a href="/main-info"> </a> /      </p>

                {bodies.length === 0 ? (
                    <p>    .</p>
                ) : (
                    bodies.map((body, index) => (
                        <div key={index} className="management-body-card">
                            <h2>{body.ManagementBodiesName}</h2>
                            <p><strong>:</strong> {body.ManagementBodiesNumber}</p>
                            <p><strong>Email:</strong> {body.ManagementBodiesEmail}</p>
                            <p><strong>:</strong> . {body.ManagementBodiesCity}, . {body.ManagementBodiesStreet}, . {body.ManagementBodiesHome}</p>
                            <p><strong> :</strong> {body.ManagementBodiesSurnameEmployee} {body.ManagementBodiesNameEmployee} {body.ManagementBodiesPatronymicEmployee}</p>
                            <p><strong>:</strong> {body.ManagementBodiesPositionEmployee}</p>
                            <p><strong>-:</strong> <a href={body.ManagementBodiesSite} target="_blank" rel="noopener noreferrer">{body.ManagementBodiesSite}</a></p>
                            <p><strong> :</strong> {body.ManagementBodiesOperatingMode}</p>
                        </div>
                    ))
                )}
            </div>
        );
    }
}

export default ManagmentBodies;
